#root {
  max-width: 80%;
  margin: 50px auto;
}
.block {
  margin-top: 50px;
}
form {
  text-align: center;
}

input {
  width: 30%;
  height: 30px;
  margin-bottom: 15px;
}

button {
  padding: 10px;
}

ul {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

li {
  list-style-type: none;
  border: 1px solid #333;
  flex: 0 1 30%;
  padding: 10px;
  margin: 5px;
}

p {
  line-height: 10px;
}

span {
  font-weight: bold;
}
